/* BODY */
body {
  position: relative;
  font-size: 16px;
  margin: 0;
  font-weight: 400;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  a{
    text-decoration: none;
    &:hover{
      text-decoration: none;
    }
  }
}

*, :after, :before {
  box-sizing: inherit;
}

.container-wrapper {
  //width: 1180px;
  margin: 0 auto;
  position: relative;
  @media all and (max-width: 1179px){
    width: 100%!important;
    padding: 0px 15px;
  }
}

:focus {
  outline: none;
}



